@import "../../scss/variables", "../../scss/mixins";

.navbar{

    width: 100%;
    display: flex;
    justify-content: space-evenly;
    background-color: $cardNavBgColor;

    a{
        color: #fff;
        font-size: 2rem;
        padding: 1rem;
        text-decoration: none;
        font-weight: bold;

        &:hover{
            
            background-color: $backgroundColor;
            color : $cardNavBgColor;
        }
}
}

@include media-xsm {
    .navbar {
      flex-direction: column;
      text-align: center;
    }
  }
  @include media-sm {
    .navbar {
      flex-direction: row;
    }
  }